import React, { useEffect, useContext, useState } from 'react';
import classes from './Chatting.module.scss';
import pic from '../../assets/jeffrey_000.png';
import { Header } from './Header/Header';
import { ChattingMessages } from './ChattingMessages/ChattingMessages';
import { ChattingSend } from './ChattingSend/ChattingSend';
import { useParams } from 'react-router-dom';
import { socket } from '../../socket';
import AuthContext from '../../context/Auth/AuthContext';

export const Chatting = () => {
  const { user } = useContext(AuthContext);
  const { username } = useParams();
  const [isMounted, setIsMounted] = useState(false);
  const [error, serError] = useState(null);
  const [msgs, setMsgs] = useState([]);
  const [msg, setMsg] = useState('');

  useEffect(() => {
    console.log('render');
  });

  useEffect(() => {
    setIsMounted(true);
    if (isMounted) {
      serError(null);
      setMsg(null);
      setMsg('');
    }
    return () => setIsMounted(false);
  }, [username, isMounted]);

  useEffect(() => {
    if (isMounted) {
      if (username === user.username) {
        serError(
          <h1 className={classes.Error}>You can't chat with yourself</h1>
        );
      } else {
        socket.emit('room', { curUser: user.username, chatWith: username });
        socket.on('getAllMsgs', (data) => {
          setMsgs(data);
        });
      }
    }
    return () => setIsMounted(false);
  }, [user, username, isMounted]);

  useEffect(() => {
    socket.on('sendmsg', (data) => setMsgs((prev) => [...prev, data]));
  }, []);

  const sendMsgHandler = () => {
    if (msg.trim() === '') {
      return setMsg('');
    }
    socket.emit('sendmsg', {
      message: msg,
      from: user.username,
      to: username,
    });
    setMsg('');
  };

  return (
    <div className={classes.Chat}>
      <Header photo={pic} name={username} />
      {error ? (
        error
      ) : (
        <>
          <ChattingMessages msgs={msgs} />
          <ChattingSend sendMsg={sendMsgHandler} msg={msg} setMsg={setMsg} />
        </>
      )}
    </div>
  );
};
