import React from 'react';
import classes from './ChattingSend.module.scss';
import { TextareaAutosize as Textarea } from '@material-ui/core';
import { Button } from '../../../components/Button/Button';
import { FiSend } from 'react-icons/fi';

export const ChattingSend = (props) => {
  return (
    <div className={classes.Send}>
      <Textarea
        placeholder="Type a message..."
        className={classes.SendTextarea}
        value={props.msg}
        onChange={(e) => props.setMsg(e.target.value)}
        onKeyDown={(e) => {
          if (e.keyCode === 13 && !e.shiftKey) {
            e.preventDefault();
            props.sendMsg();
          }
        }}
      />
      <Button className={classes.SendBtn} onClick={props.sendMsg}>
        <FiSend />
      </Button>
    </div>
  );
};
