import React from 'react';
import classes from './ChatMessage.module.scss';

export const ChatMessage = (props) => {
  const msgCreatedAt = new Date(props.createdAt);

  const time = `${msgCreatedAt.getHours()}:${msgCreatedAt.getMinutes()}`;

  let msgUrl = props.msg.replace(
    /((\w+:\/\/\S+)|(\w+[.:]\w+\S+))[^\s,.]/gi,
    (match) =>
      `<a target="_blank" rel="noopener noreferrer" href=${match}>${match}</a>`
  );

  msgUrl = msgUrl.replace(
    /\b[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}\b/g,
    (match) => `<a href=${match}>${match}</a>`
  );

  return (
    <div
      className={[
        classes.Container,

        props.type === 'recevied'
          ? classes.Recevied
          : props.type === 'sent'
          ? classes.Sent
          : null,
      ].join(' ')}
    >
      <p
        className={classes.Msg}
        dangerouslySetInnerHTML={{
          __html: msgUrl,
        }}
      ></p>
      <p className={classes.Date}>{time}</p>
    </div>
  );
};
